// eslint-disable-next-line
import weui from 'weui';
export default {
  name: 'app',
  data() {
    return {
      bridgeReady: false
    };
  },
  methods: {
    closeWindow() {
      if (this.bridgeReady) {
        // eslint-disable-next-line
        AlipayJSBridge.call('closeWebview');
      }
    },
    onBridgeReady() {
      this.bridgeReady = true;
    }
  },
  created() {
    if (typeof AlipayJSBridge === 'undefined') {
      document.addEventListener('AlipayJSBridgeReady', this.onBridgeReady, false);
    } else {
      this.onBridgeReady();
    }
  }
};



